@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
.header-container {
	background: linear-gradient(135deg,#ff5f6d, #FFC371);
	padding: 24px 0px 24px 0px;
	display: flex;
}
.header-container .nav-left .__navbar{
	display: flex;
	list-style-type : none;
	align-items: center;
}
.header-container .nav-right .__navbar{
	display: flex;
	list-style-type : none;
	align-items: center;
}
.header-container .__navbar li {
	padding: 12px;
}
.header-container .__navbar li a {
	color: black;
	font-size: 24px;
	font-weight: 600;
}
.nav-right {
	display: flex;
	position: absolute;
	right: 36px;
}
.container {
	display: flex;
	flex-wrap: wrap;
}
.wapper-btn {
	margin: 12px;
	display: flex;
}
.button {
	padding : 12px 20px;
	border-radius : 10px;
	border-width : 0;
	cursor: pointer;
	border-color : '#E8E8E8';
	background-color : '#F6F6F6';
}
.button_active {
	padding : 12px 20px;
	border-radius : 10px;
	border-width : 0;
	cursor: pointer;
	border-color : '#E8E8E8';
	background-color : bisque;
	color: crimson;
	font-weight: 600;
}
*{
	font-family: 'Montserrat', sans-serif;
	box-sizing: border-box;
}
